@import '~node_modules/modern-normalize/modern-normalize.css';
@import 'src/sass/utils/_vars.scss';

* {
    margin-block-start: 0px;
    margin-block-end: 0px;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
  }

body {
    font-family: 'DM Sans';
    font-weight: 400px;
    font-style: normal;
    font-size: 14px;
     
}

.arrow-button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border-style: none;
  background-color: $whiteColor;
}
.arrow-button__svg {
  fill: #d41443;
}


